@import url(https://fonts.googleapis.com/css2?family=Work+Sans:wght@100;200;300;400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: 'Autopia-Regular';
  src: url(/jawblia/portfolio/static/media/Autopia-Regular.b6db5d2b.woff);
}


:root {
  --body: #1F3304;
  --body-light: hsla(86, 85%, 11%, 0.5);
}

.App {
  height: 100vh;
  display: flex;
  background-color: rgb(103, 104, 99);
  background: radial-gradient(circle, #B7D0D7 0%, #F1DEB7 100%), 
  radial-gradient(ellipse at bottom, #FCC482 0%, rgb(80, 194, 226) 100%), 
  linear-gradient(121.28deg, #FFEAB6 0%, rgb(86, 0, 0) 100%), 
  linear-gradient(229.79deg, #7534FF 0%, #248900 94.19%), 
  radial-gradient(56.26% 101.79% at 50% 0%, #8F00FF 0%, #493500 100%), 
  linear-gradient(65.05deg, #D4DFE3 0%, #FFD600 100%);
  background-blend-mode: overlay, screen, color-burn, hard-light, screen, normal;
}

body {
  color: #1F3304;
  color: var(--body);
  font-size: 62.5%;
  font-family: 'Work Sans', sans-serif;
  font-weight: 100;
  cursor: crosshair;
  transition: all .3s;
}

h1 {
  font-size: 2rem;
  font-weight: 200;
  cursor: help;
}

h3 {
  font-family: 'Work Sans', sans-serif;
  font-weight: 300;
  font-size: 1rem;
}

.heavy {
  font-family: 'Work Sans', sans-serif;
  font-weight: 500;
}

.heavy:hover {
  font-style: italic;
}

h4 {
  font-family: 'Work Sans', sans-serif;
  font-weight: 400;
  font-size: 1rem;
}

p {
  font-size: .75rem;
  font-weight: 400;
}

a {
  text-decoration: none;
  color: #1F3304;
  color: var(--body);
}

.left--wrapper {
  display: flex;
  flex-direction: column;
  height: 100%;
  flex: 0 1 40%;
  background-image: radial-gradient(ellipse, rgba(183, 208, 215, 0.658) 0%, transparent 50%);
  justify-content: flex-end;
  align-items: flex-start;
}

.card {
  width: 25%;
  height: 20vh;
  padding: 1rem;
  margin: 3rem;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  border: 5px solid rgba(255, 255, 255, 0.4);
  border-radius: 1rem;
  box-shadow: 20px 20px 60px #e7f0db, 
  -20px -20px 60px #fffff2;
}

.card--item {
  display: flex;
  align-items: baseline;
  padding-bottom: 0.5rem;
}

.card--item:hover {
  font-style: italic;
}

.card--item:hover > .card--empty {
  background-color: #1F3304;
  background-color: var(--body);
}

.card--empty {
  height: .75rem;
  width: .75rem;
  border: 1px solid #1F3304;
  border: 1px solid var(--body);
  border-radius: 50%;
  margin-right: 1rem;
}

.social {
  font-size: 1.5rem;
}

.social--row {
  width: 80%;
  display: flex;
  justify-content: space-between;
  margin-bottom: -1rem;
}

.social__icon{
  height: 1rem;
}

.title--card {
  width: 60%;
  height: 30vh;
  border-radius: 1rem;
  margin: 3rem;
  padding: 3rem;
  border: 5px solid rgba(255, 255, 255, 0.4);
  box-shadow:  20px 20px 60px #e7f0db, 
  -20px -20px 60px #fffff2;
  overflow: hidden;
}

.title--text {
width: 90%;
border-top: 1px solid #1F3304;
border-top: 1px solid var(--body);
}

.box--line{
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  width: 90%;
  padding-bottom: 1rem;
}

.box--name {
  display: flex;
  height: 1rem;
  width: 50%;
  border-bottom: 1px solid #1F3304;
  border-bottom: 1px solid var(--body);
  margin-right: 1rem;
}

.box--date {
  display: flex;
  height: 1rem;
  width: 20%;
  border-bottom: 1px solid #1F3304;
  border-bottom: 1px solid var(--body);
}

.box--signature {
  display: flex;
  height: 1rem;
  width: 85%;
  border-bottom: 1px solid #1F3304;
  border-bottom: 1px solid var(--body);
}

.text--line:not(:first-child) {
  padding-top: 1rem;
}

.right--wrapper {
  display: flex;
  flex-direction: column;
  height: 100%;
  flex: 0 1 60%;
  overflow: scroll;
}

.project--card {
  display: flex;
  flex-direction: column;
  width: 75%;
  border: 1px solid #1F3304;
  border: 1px solid var(--body);
  padding: 1rem;
  margin: 3rem 1rem;
  border-radius: 1em;
  border: 5px solid rgba(255, 255, 255, 0.4);
  box-shadow:  7px 7px 14px #efece1;
}

.project--img-gallery {
  display: flex;
  justify-content: flex-start;
}

.project--img {
  max-width: 25%;
  height: auto;
}

.project--img__large {
  max-width: 85%;
  height: auto;
  border: 5px solid rgba(255, 255, 255, 0.4);
}

.cursor {
  width: 8rem;
  height: 8rem;
  background-image: radial-gradient(rgba(0, 2, 110, 0.479) 10%, transparent 75%);
  border-radius: 100%;
  position: fixed;
  transform: translate(-50%, -50%);
  pointer-events: none;
  z-index: 10;
  mix-blend-mode: difference;
}

.close {
  margin-top: 5rem;
}

.page-wrapper {
  margin-right: 5rem;
}
